<template>
    <div class="container vc--resume">
        <div class="row justify-content-center align-items-center">
            <div class="vc--resume col-md-12 justify-content-center align-items-center" ref="resume">
                <!--<el-alert v-if="alert.message != null && alert.message != undefined && alert.message.length > 0 " :title="alert.message" :type="alert.type" style="margin-bottom:5px;"></el-alert>-->
                <div v-loading="loading"
                    element-loading-text="Đang tải dữ liệu..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(255, 255, 255, 0.8)">
                    <resume-content v-if="!print && !loading" :profile="profile"></resume-content>
                    <print-content v-if="print && !loading" :profile="profile"></print-content>
                </div>
            </div>
            <aside class="vc--toolbar" v-if="!loading && !print">
                <ul>
                    <li>
                        <a href="#" @click.prevent="doPrint"><i class="fa fa-print"></i>&nbsp;Print</a>
                    </li>
                    <li>
                        <a href="#" @click.prevent="doExportPng"><i class="fa fa-image"></i>&nbsp;PNG</a>
                    </li>
                </ul>
            </aside>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex';
    import html2canvas from 'html2canvas';
    import FileSaver from 'file-saver';

    import ResumeContent from './components/ResumeContent.vue';
    import PrintContent from './components/PrintContent.vue';

    export default {
        name:'App',
        computed: {
            ...mapState({
                loading:  state => state.loading,
                print:  state => state.print,
                profile:  state => ( state.profile != null && state.profile[state.language] != null ) ? state.profile[state.language] : null,
                alert:    state => state.alert
            })
        },

        methods:{
            doPrint() {
                let me = this;
                
                var onPrintFinished = function() {
                    
                    me.$store.dispatch('setPrint', false);
                }
                this.$store.dispatch('setPrint', true);
                
                setTimeout( ()=> { 
                    onPrintFinished(window.print()); 
                }, 1500);
                
            },
            doExportPng() {
                let me = this;

                this.$store.dispatch('setPrint', true);
                
                setTimeout( () => { 
                    html2canvas(me.$refs.resume)
                    .then(function (canvas) {
                        canvas.toBlob(function (blob) {
                            FileSaver.saveAs(blob, 'cv.png');
                            me.$store.dispatch('setPrint', false);
                        })
                    });
                }, 1500);
            }
        },

        mounted() {
            this.$store.dispatch('getResume');
        },

        components: {
            ResumeContent,
            PrintContent
        }
    };
</script>

