
import Vue from 'vue';
import Vuex from 'vuex';
import ResumeDemo from '../../public/static/resume.json';

Vue.use(Vuex);

export const SET_LANGUAGE  = 'set_lang';
export const SET_PRINT = 'set_print';
export const SET_ERROR = 'set_error';
export const BEFORE_GET_RESUME = 'before_get_resume';
export const GET_RESUME = 'get_resume';
export const SET_RESUME_PAGE = 'set_resume_page';

const state = {
    loading: false,
    profile: ResumeDemo,
    page: 1,
    print: false,
    url: window.__URL__ || '/static/resume.json',
    language: 'en',
    alert:{
        code: 200,
        type:'success',
        message:'Success'
    }
};

const actions = {

    getResume: ({commit, state}) => {
        if( state.url == null ){ return ;}
        commit(BEFORE_GET_RESUME);

        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = () => {
            if(xhr.readyState === 4) {
                if(xhr.status >= 200 && xhr.status < 300) {
                    try {
                        let resume = JSON.parse(xhr.responseText);
                        commit(GET_RESUME,{
                            profile: resume
                        });
                    } catch(e) {
                        commit(SET_ERROR, {
                            code:500,
                            type:'error',
                            message: 'Parse cv failed'
                        });
                    }
                } 
                else {
                    commit(SET_ERROR, {
                        code: 500,
                        type:'error',
                        message: 'request was unsuccessful: ' + xhr.status
                    });
                }
            }
            
        };
        xhr.open('GET', state.url, true);
        xhr.send(null);
    },

    setPage: ({commit}, page) => {
    
        commit({ 
            type:SET_RESUME_PAGE,
            page:parseInt(page) 
        });
    },

    setPrint: ({commit}, isPrint) => {
    
        commit(SET_PRINT, {
            print:isPrint
        });  
    },

    setLanguage: ({commit}, lang) => {
    
        commit(SET_LANGUAGE,{
            lang:lang
        });  
    }
}

const mutations = {
    [BEFORE_GET_RESUME] (state) {
        state.loading = true;
    },

    [GET_RESUME] (state, payload) {
        
        state.profile  = payload.profile;
        state.loading = false;
    },

    [SET_RESUME_PAGE] (state, payload) {
        state.page  = parseInt(payload.page);
    },

    [SET_PRINT] (state, payload) {
        state.print  = payload.print;
    },

    [SET_ERROR] (state, payload) {
        state.loading = false;
        state.alert = payload
    },

    [SET_LANGUAGE] (state, payload) {
        state.language = payload.lang
    }
};

const getters = {
    getFetching: (state) => state.loading,
    getPage: (state) => state.page,
    getPrint: (state) => state.print,
    getProfile: (state) => ( state.profile != null && state.profile[state.language] != null ) ? state.profile[state.language] : null
}
export default new Vuex.Store({
    state,
    actions,
    getters,
    mutations
});

